@import url("https://fonts.googleapis.com/css2?family=Chivo+Mono:wght@100;200;300&family=Public+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Rubik:wght@300&display=swap");

header {
  background: linear-gradient(90deg, #2176ff 0.7%, #1ca3e9 98.1%);
}
body {
  font-family: "Public Sans";
}
// Nav Section
.container {
  max-width: 1488px;
  margin: auto;

  nav {
    ul {
      position: relative;
      li {
        padding: 1.9rem 1rem;
        font-size: large;
        a {
          text-decoration: none;
          color: white;
          padding-right: 5px;
        }

        img {
          filter: brightness(0) invert(1);
        }
      }
    }
  }

  .nav-btn button {
    background-color: #f8f9fa;
    border: 1px solid #f8f9fa;
    border-radius: 50rem;
    color: #212529;
    display: inline-block;
    font-size: 1.1rem;
    font-weight: 700;
    line-height: 1.5;
    padding: 0.5rem 1rem;
    text-align: center;
    color: #2176ff;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
      border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    -webkit-user-select: none;
    user-select: none;
    vertical-align: middle;
  }
}

// Nav Section End

// HeroSection Strat

main {
  background-image: url(./Assests/HeroBackgroundimg.svg);
  background-color: #fff;
  background-position: 100% 100%;
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  padding-bottom: 150px;
  padding-top: 99px;
  width: 100%;
  height: 42rem;

  .hero-Content {
    h1 {
      font-size: 3.5rem;
      font-weight: 700;
    }
    p {
      margin-top: 1.2rem;
      margin-bottom: 3rem;
    }
    button {
      background-color: #fcd70d !important;
      border: 1px solid #fcd70d !important;
      border-radius: 50rem;
      color: #000 !important;
      display: inline-block;
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      padding: 0.5rem 2rem;
    }
  }
}
.Dropdown {
  background: aliceblue;
  padding: 2rem 1rem;
  position: absolute;
  top: 88px;
  padding-right: 3rem;
  border-radius: 12px;
  .dropdown-items {
    p {
      margin-bottom: 0;
      margin-left: 5px;
    }

    .boder-bottom {
      border-top: 1px solid #e9ecef;
      height: 0;
      margin: 0.5rem 0;
      overflow: hidden;
    }
  }
}

@media screen and (max-width: 1515px) {
  nav {
     ul{
      li {
        a {
          font-size: smaller;
        }
      }
    }
  }
  main {
    .container {
      max-width: 1100px;
      margin: auto;
    }
  }
}
@media screen and (max-width: 1172px) {
  .container nav ul li{
    padding: 1.9rem 0.5rem;
  }
  main{
    background: #2176ff;
    height: fit-content;
  }
  main {
    .container {
      max-width: 950px;
      margin: auto;
    }
  }


}
@media screen and (max-width: 1100px) {
 nav, .nav-btn{
  display: none;
 }
  .InvoLogo{
    padding: 1rem 0;
  }
  
}
@media screen and (max-width: 1000px) {
  main {
    padding-bottom: 40px;
    .container {
      max-width: 750px;
      margin: auto;
    }
  }

  .hero-Img img{
    margin-top: 4rem;
  }
}
@media screen and (max-width: 850px) {
  main {
    .container {
      max-width: 650px;
      margin: auto;
    }
  }
}
@media screen and (max-width: 850px) {
.hero-Content{
  button {
    width: 100%;
  }
}
}
@media screen and (max-width: 387px) {
  main .hero-Content h1{
    font-size: 2.4rem;
  }
}
